"use client";
import { useState } from "react";
import { Calendar, Flame, Clock3, School, DollarSign } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import bgImage from "@/public/images/bg-22.png"
const filters = [
    { label: "Weekdays", icon: Calendar },
    { label: "Popular", icon: Flame },
    { label: "Latest", icon: Clock3 },
    { label: "University", icon: School },
    { label: "Price", icon: DollarSign },
];


const events = Array.from({ length: 15 }, (_, i) => ({
    title: "Wonder Girls 2010 Wonder Girls World Tour",
    date: "14",
    month: "May",
    location: "University of Moratuwa",
    image: `/images/1 (${(i % 5) + 1}).jpg`,
    tag: i % 3 === 0 ? "HOT" : i % 4 === 0 ? "NEW" : null,
}));

export default function AllEventsPage() {
    const [activeFilter, setActiveFilter] = useState("Weekdays");

    return (
        <main className="bg-white min-h-screen">
            <section className="relative flex items-center justify-center h-[450px] py-20 text-white text-center">
                {/* الخلفية مع الطبقة الشفافة */}
                <div className="absolute inset-0 z-0">
                    <Image
                        src={bgImage}
                        alt="Modern Purple Background"
                        layout="fill"
                        objectFit="cover"
                        className="brightness-75"
                    />
                    <div className="absolute inset-0 bg-gradient-to-r from-purple-900/60 to-indigo-900/40" />
                </div>
                <h1 className="text-4xl rounded-2xl px-4 py-6 bg-gradient-to-r from-pink-500 to-purple-700 text-gray-300/90 font-bold absolute z-1 text-center">All Events</h1>
            </section>
            <div className="max-w-6xl mx-auto px-4 py-10">
                {/* Filters */}
                <div className="flex flex-wrap justify-center gap-4 mb-10">
                    {filters.map(({ label, icon: Icon }) => (
                        <button
                            key={label}
                            onClick={() => setActiveFilter(label)}
                            className={`flex items-center gap-2 px-4 py-2 rounded-full border transition ${activeFilter === label
                                ? "bg-purple-600 text-white"
                                : "bg-white text-gray-800 border-gray-300 hover:bg-purple-100"
                                }`}
                        >
                            <Icon size={18} />
                            <span className="text-sm font-medium">{label}</span>
                        </button>
                    ))}
                </div>

                {/* Events grid */}
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
                    {events.map((event, idx) => (
                        <Link href={`/events/${event.idx}`} key={idx}>
                            <div
                                className="bg-white cursor-pointer hover:shadow-lg transition rounded-xl shadow-md overflow-hidden relative"
                            >
                                {event.tag && (
                                    <div className="absolute top-3 right-3 bg-red-500 text-white text-xs font-bold px-2 py-1 rounded-full z-10">
                                        {event.tag}
                                    </div>
                                )}
                                <Image
                                    src={event.image}
                                    alt={event.title}
                                    width={400}
                                    height={200}
                                    className="w-full h-48 object-cover"
                                />
                                <div className="p-4">
                                    <div className="text-sm text-gray-500 mb-1">
                                        {event.date} {event.month}
                                    </div>
                                    <h3 className="text-lg font-semibold leading-snug line-clamp-2 mb-2">
                                        {event.title}
                                    </h3>
                                    <p className="text-sm text-gray-600 line-clamp-1">{event.location}</p>
                                </div>
                            </div>
                        </Link>
                    ))}
                </div>
            </div>
        </main >
    );
}